.fb-advanced-button-wrapper {
    .fb-advanced-button {
        align-items: center;
        display: inline-flex;
        justify-content: center;
        outline: none;
        text-align: center;
        text-decoration: none;
        border-radius: 0px;
        box-sizing: border-box;
        cursor: pointer;
        position: relative;
        transition: all .3s ease-in-out;
        white-space: nowrap;
        &:focus {
            outline: none;
            box-shadow: none;
        }
        .fb-advanced-button-text {
            transition: all .3s ease-in-out;
        }
        .fb-advanced-button-icon-wrapper {
            display: flex;
            align-items: center;
            &.left {
                margin-right: 10px;
            }
            
            &.right {
                margin-left: 10px;
            }
    
            .fb-advanced-button-icon {
                display: flex;
                align-items: center;
                justify-content: center;
                position: relative;
                z-index: 1;
                svg {
                    display: block;
                    width: 16px;
                    height: 16px;
                    box-sizing: content-box;
                }
            }
            .fb-advanced-button-icon-image{
                display: flex;
                align-items: center;
                justify-content: center;
                position: relative;
                z-index: 1;
                width: 20px;
                height: 20px;
                img {
                    width: 100%;
                    height: 100%;
                    display: block;
                    box-sizing: content-box;
                }
            }        
        }
    }
}